import React, {Component} from 'react';
import './Login.css'
import {Link} from 'react-router-dom';

class Login extends Component{
    constructor(){
        super();
        this.state ={
            value : "Rishi",
        }
    }

    display(name){
        this.setState({value:name.target.value})
    }

    render(){
        return(
            <div className="log">
                <h1>Login Page</h1>
                <form>
                    <table>
                        <tr>
                            <td>
                                <label>Name:</label>
                            </td>
                        </tr>
                        <tr>
                            <td>
                                <input type="text"></input>
                            </td>
                        </tr>
                        <tr>
                            <td>
                                <label>Password:</label>
                            </td>
                        </tr>
                        <tr>
                            <td>
                                <input type="password"></input>
                            </td>
                        </tr>
                        <tr>
                            <td>
                                <Link to={"/main"}><input type="submit" value="Log in"></input></Link>
                            </td>
                        </tr>
                    </table>
                </form>
            </div>
        );
    }
}

export default Login;