import React, {Components} from 'react';
import {Link} from 'react-router-dom';
import './Nav.css'

const Nav = () =>{
    return(
        <div className="header">
            <div className="nav">
                <h3>Logo</h3>
                {/* <input type="text" placeholder="Enter url for left div"></input>
                <input type="text" placeholder="Enter url for left div"></input> */}
                <Link to={"/"}>
                <button>Log out</button>
                </Link>
            </div>
        </div>
    )
}

export default Nav;